.upbtn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: $color-chinook;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url('../img/svg/up-arrow.svg');
  border: none;
  position: fixed;
  bottom: 32px;
  right: 32px;
  cursor: pointer;

  &:hover {
    background-color: $color-medium-sea-green;
  }

  &:active {
    background-color: rgba($color-medium-sea-green, 0.4);
  }

  &--disabled {
    display: none;
  }
}
